
main {
	
	#calculator_ups {
		.fieldset_container {
			padding: 5px;
			box-sizing: border-box;
		}
		.fieldset_container-active {
			border: 1px solid red;
			box-shadow: 0 0 1px;
		}
		.line-active {
			&:hover {
				background: red;
			}
		}
		width: 800px;
		fieldset {
				input {
					height: 38px;
					padding: 8px 10px;
					box-sizing: border-box;
				}
				input[type="number"] {
					-moz-appearance: textfield;
					&::-webkit-inner-spin-button, 
					&::-webkit-outer-spin-button { 
				  		-webkit-appearance: none; 
				  		margin: 0; 
					}
				}		
			.textrow {
				padding: 5px;
				> .text-left {
					text-align: left;
					display: inline-block;
					h3 {
						font-size: 16px;
					}
				}
			}
			.dropdown, .datainputs {
				display: inline-block;
			}
		}

		#fieldset_standard {
			
			input[type="number"] {
				width: 50%;
			}

			input[type="text"] {
				width: 100%;
			}

			.dropdown {
				width: 33.3333%;
				position: relative;
			}

			.datainputs {
				width: 66.6666%;
			}

			.textrow > .text-left {
				width: 33.33333%;
			}
		}

		#fieldset_amplifiers {

			input[type="text"] {
				width: 100%;
			}
			input[type="number"] {
				width: 50%;
			}
			
			.amplifier_name, .dropdown {
				width: 23.5%;
				display: inline-block;
			}
			.datainputs {
				width: 47%; 	
			}
			.textrow > .text-left {
				width: 23.5%;
				&:first-of-type {
					width: 6%;
				}
			}
		}
		#fieldset_otheritems {
			.textrow > div:first-child {
				width: 6%;
			}
			button#remove_otheritem {
				margin-bottom: 10px;
			}
		}
		#calculate {
			display: block;
			padding: 10px 14px;
			color: white;
			border: none;
			background: #ee5c42;
			font-size: 18px;
			margin: 10px auto;
		}
		.result, .error {
			text-align: center;
		}
	}
	.button_addition {
		display: inline-block;
		width: 6%;
	}
	button[type="button"] {
		background: #49E20E;
		width: 30px;
		height: 30px;
		border: none;
		border-radius: 50%;
		color: white;
		font-weight: 600;
		padding: 0;
		font-size: 17px;
		top: 4px;
		position: relative;
		&:focus {
			outline: none;
		}
	}
	button.btn_remove, #remove_amplifier, #remove_otheritem {
		background: red;
	}
}